.alert-add {
  transform: translate(-50%);
}

.alert-remove {
  transform: translate(-50%, -800%);
}

.alert-color-positive {
  color: #228b5a;
  background-color: #8de29d;
  border-bottom: .5rem solid #35a772;
  border-left: .5rem solid #35a772;
  border-right: .5rem solid #35a772;
  font-weight: 600;
}

.alert-color-negative {
  color: #8b2222;
  background-color: #e28d8d;
  border-bottom: .5rem solid #b63e29;
  border-left: .5rem solid #b63e29;
  border-right: .5rem solid #b63e29;
  border-radius: 5px;
  font-weight: 600;
}

.add-answer-btn {
  width: 30%;
  color: #fff;
  background-color: #2e67d1;
  border: none;
  border-radius: 10px;
  align-self: center;
  margin: 1rem;
  padding: .8rem;
  font-size: 1.8rem;
  font-weight: 450;
  text-decoration: none;
  transition: all .2s;
}

